.header {
  display: flex;
  .header__photo {
    width: 42%;
    background: url("../../assets/HeroImage.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    @media (min-width: 1900px) {
      background-size: contain;
    }
  }
  .header__mainDiv {
    width: 58%;
    padding-top: 2.31rem;
    .header__links {
      font-size: 0.875rem;
      color: $color-alt2;
      display: flex;
      justify-content: flex-end;
      padding: 0 8.9rem 0 23rem;
      margin-bottom: 2rem;
      .header__logIn {
        text-decoration: none;
        margin-right: 2rem;
        padding-top: 0.6rem;
        &:visited {
          color: $color-alt2;
        }
      }
      .header__register {
        text-decoration: none;
        border: solid 1px $color-alt-yellow;
        padding: 0.6rem;
        &:visited {
          color: $color-alt2;
        }
      }
    }
    .header__scrollLinks {
      font-size: 1.125rem;
      display: flex;
      justify-content: space-between;
      padding: 0 8.9rem 0 11rem;

    }

    .header__div {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 93%;
      justify-content: center;
      padding: 7rem 10rem 12rem 0;
      .header__mainText {
        font-size: 2.375rem;
        text-align: center;
        margin-bottom: 1.5rem;
      }
      .header__btns {
        display: flex;
        width: 100%;
        justify-content: space-around;
        margin-top: 4.8rem;
        padding: 0 5rem 0 5rem;
        .btn {
          @include button(2.25rem);
          text-align: center;
          font-weight: lighter;
          color: $color-alt3;
          &:visited {
            color: $color-alt3;
          }
        }
      }
    }
  }
}
